import React from 'react';
import { Row , Col} from 'react-bootstrap';
import './MenuGrid.css'

const MenuGrid = () => {
    const gridItems = [
        {name: 'Beef Burger', img: 'https://assets.suelo.pl/soup/img/products/product-burger.jpg', id: 1},
        {name: 'Broccoli', img: 'https://assets.suelo.pl/soup/img/products/product-pizza.jpg', id: 2},
        {name: 'Chicken Burger', img: 'https://assets.suelo.pl/soup/img/products/product-chicken-burger.jpg', id: 3},
        {name: 'Creste di Galli', img: 'https://assets.suelo.pl/soup/img/products/product-pasta.jpg', id: 4},
        {name: 'Chicken wings', img: 'https://assets.suelo.pl/soup/img/products/product-chicken-wings.jpg', id: 5},
        {name: 'Nigiri-sushi', img: 'https://assets.suelo.pl/soup/img/products/product-chicken-wings.jpg', id: 6},
    ]
    return (
        <section>
            <div className="gridBanner">
                <img src="https://assets.suelo.pl/soup/img/photos/menu-title-burgers.jpg" alt="" />
            </div>
            <div className="gridBox">
                <Row>
                    {
                        gridItems.map(({name, img, id}) => {
                            return(
                                <Col md={4} sm={6} key={id}>
                                    <div className="gridDetails">
                                        <img src={img} alt="" />
                                        <h6>{name}</h6>
                                        <span>Beef, cheese, potato, onion, fries</span>
                                        <div className="productPrice">
                                            <span>from <span className="text-white">â‚¹ 9.00</span></span>
                                            <button className="animateBtn">Add to cart</button>
                                        </div>
                                    </div>
                                </Col>
                            )
                        })
                    }
                </Row>
            </div>
        </section>
    );
};

export default MenuGrid;