import React from 'react';
import MenuGrid from '../components/MenuGrid/MenuGrid';
import { Container, Row , Col} from 'react-bootstrap';
import { Link } from "react-router-dom";

const Menu = () => {

    return (
        <section width="100%">
            <Container>
                <Row>
                    <Col md={4}>
                        <ul>
                            <li>
                                <Link to="/">BURGERS</Link>
                            </li>
                            <li>
                                <Link to="/">PASTA</Link>
                            </li>
                            <li>
                                <Link to="/">PIZZA</Link>
                            </li>
                            <li>
                                <Link to="/">SUSHI</Link>
                            </li>
                            <li>
                                <Link to="/">DESSERTS</Link>
                            </li>
                            <li>
                                <Link to="/">DRINKS</Link>
                            </li>
                        </ul>
                    </Col>
                    <Col md={8}>
                        <MenuGrid/>
                    </Col>
                </Row>
            </Container>
        </section>
    );
};

export default Menu;